html {
  box-sizing: border-box;
  font-size: 62.5%; }

*,
*:before,
*:after {
  box-sizing: inherit; }

@font-face {
  font-family: "Houschka";
  src: url("../fonts/Houschka.ttf"); }

body {
  font-family: "Houschka"; }
  body.fix-body {
    overflow: hidden; }

.container {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto; }

h1,
h2,
h3 {
  margin: 0; }

h1 {
  font-size: 3.6rem; }

h2 {
  font-size: 3rem; }

h3 {
  font-size: 2.6rem; }

p {
  font-size: 2rem;
  color: #212121; }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

.header {
  background-color: #212121; }
  .header .header-content {
    padding: 2rem 0; }
    @media (min-width: 768px) {
      .header .header-content {
        display: flex;
        justify-content: space-between;
        align-items: center; } }
  .header h1 {
    color: #ffffff;
    margin-bottom: 1rem;
    text-align: center; }
    @media (min-width: 768px) {
      .header h1 {
        margin-bottom: 0; } }
  .header.fixed-top {
    position: fixed;
    width: 100%;
    box-shadow: 0 .3rem .4rem #333; }

.main-navigation {
  display: flex;
  gap: 2rem;
  justify-content: center; }
  .main-navigation a {
    color: #ffffff;
    font-size: 1.8rem;
    font-weight: bold;
    letter-spacing: 1px; }
  .main-navigation a:hover {
    color: #bdbdbd; }

.video {
  position: relative;
  max-height: 86rem;
  overflow: hidden; }
  .video .overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.5); }
  .video video {
    display: block;
    width: 100%; }

.video-content {
  display: flex;
  align-items: center;
  height: 100%;
  position: relative;
  z-index: 1; }
  .video-content h2 {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    color: #212121;
    font-size: 6rem; }
    .video-content h2 span {
      color: #ffffff;
      background-color: #212121;
      font-size: 2rem;
      padding: 1rem 2rem;
      border-radius: .4rem; }

.about-festival {
  padding: 5rem 0; }
  @media (min-width: 768px) {
    .about-festival {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 5rem; } }
  .about-festival .image img {
    max-width: 100%;
    border-radius: .3rem; }

.festival-content {
  display: flex;
  flex-direction: column;
  justify-content: center; }
  .festival-content .date {
    font-weight: bold; }
  .festival-content .text {
    line-height: 1.5;
    text-align: justify; }

.artists {
  padding: 5rem 0;
  background-color: #f5f5f5; }
  .artists h2, .artists p {
    text-align: center;
    color: #212121; }
  .artists h2 {
    text-transform: uppercase;
    letter-spacing: 2px; }
  .artists .day {
    margin: 5rem 0;
    font-weight: bold;
    text-transform: uppercase; }

@media (min-width: 768px) {
  .stages-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem; } }

.stages-container .stage {
  padding-top: 2.5rem; }

.stages-container .stage-name {
  color: #000000;
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 3rem; }

.calendar {
  padding: 0;
  list-style: none; }
  .calendar li {
    padding: 2.5rem;
    font-weight: bold;
    font-size: 1.6rem; }
    .calendar li span {
      font-weight: normal; }

.icon-container {
  text-align: center;
  margin-bottom: 4rem; }

.calendar li:nth-child(2n+1) {
  background-color: whitesmoke; }

.card {
  background-color: #ffffff;
  border-radius: .3rem;
  box-shadow: 0px 5px 15px 0px #333; }

.gallery {
  padding: 5rem 0; }
  .gallery h2 {
    text-align: center;
    text-transform: uppercase; }

.image-gallery {
  padding: 0;
  list-style: none;
  margin-top: 5rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 5rem; }
  @media (min-width: 768px) {
    .image-gallery {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      gap: 5rem; } }
  @media (min-width: 1200px) {
    .image-gallery {
      display: grid;
      grid-template-columns: repeat(6, 1fr);
      gap: 5rem; } }

.overlay {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  top: 0;
  display: grid;
  place-items: center; }

.btn-close {
  padding: .6rem 2rem;
  color: #ffffff;
  background-color: #212121;
  display: grid;
  place-items: center;
  font-weight: bold;
  font-size: 2rem;
  text-transform: uppercase;
  border-radius: .3rem; }
  .btn-close:hover {
    cursor: pointer; }

.tickets {
  padding: 5rem 0;
  background-color: #f5f5f5; }
  .tickets h2 {
    text-align: center;
    text-transform: uppercase; }
  .tickets p {
    font-size: 2.2rem;
    text-align: center;
    margin-bottom: 2rem;
    text-transform: uppercase;
    font-weight: bold; }

@media (min-width: 768px) {
  .ticket-price {
    margin-top: 2.5rem;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem; } }

@media (min-width: 768px) {
  .tickets-container {
    margin-top: 2.5rem;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem; } }

.ticket-info {
  padding: 0;
  list-style: none; }
  .ticket-info li {
    padding: 1.4rem;
    font-weight: bold;
    font-size: 1.6rem; }

.ticket-info li:first-child {
  color: #ffffff;
  text-align: center;
  text-transform: uppercase;
  background-color: #212121;
  border-bottom: none; }

.ticket-info li:last-child {
  color: #ffffff;
  text-align: center;
  text-transform: uppercase;
  background-color: #212121;
  border-bottom: none; }

.ticket-info li {
  border-bottom: 1px solid #ccc; }
